/*_________________________ Grid _________________________ */
.grid__row {
  position: relative;
  width: 100%;
  min-height: 1px; }

.grid__container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  padding: 0 0px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
  width: 100%; }
  @media only screen and (max-width: 768px) {
    .grid__container {
      max-width: 94%;
      margin: 0 auto; } }

.grid__col-1-1, .grid__col-1-2, .grid__col-1-3 {
  position: relative; }

.grid__col-1-1 {
  width: 100%; }
  @media only screen and (max-width: 768px) {
    .grid__col-1-1 {
      padding: 0px 20px 0px 20px; } }

.grid__col-1-2 {
  width: 50%; }

.grid__col-1-2:nth-of-type(odd) {
  padding-right: 10px; }

.grid__col-1-2:nth-of-type(even) {
  padding-left: 10px; }

.grid__col-1-2:nth-of-type(even) {
  padding-right: 20px;
  padding-left: 20px; }

.grid__col-1-2:nth-of-type(odd) {
  padding-left: 20px;
  padding-right: 20px; }

@media only screen and (max-width: 768px) {
  .grid__col-1-2 {
    width: 100%;
    padding: 0 20px !important; } }

.grid__col-1-3 {
  width: 33.33333%; }

.grid__col-1-3:nth-of-type(3n-2) {
  padding-right: 13.33333px; }

.grid__col-1-3:nth-of-type(3n-1) {
  padding-right: 6.66667px;
  padding-left: 6.66667px; }

.grid__col-1-3:nth-of-type(3n) {
  padding-left: 13.33333px; }

@media only screen and (max-width: 768px) {
  .grid__col-1-3 {
    width: calc(8.33333% * 12);
    padding: 0px 20px 0px 20px !important; } }

@media only screen and (max-width: 480px) {
  .grid__col-1-3 {
    padding: 0px 20px 0px 20px !important; } }

.grid__col-1-4 {
  width: 25%; }

.grid__col-1-4:nth-of-type(4n-3) {
  padding-right: 13.33333px; }

.grid__col-1-4:nth-of-type(4n-2) {
  padding-right: 6.66667px;
  padding-left: 6.66667px; }

.grid__col-1-4:nth-of-type(4n-1) {
  padding-right: 6.66667px;
  padding-left: 6.66667px; }

.grid__col-1-4:nth-of-type(4n) {
  padding-left: 13.33333px; }

.grid__col-1-6 {
  width: calc(8.33333% * 6);
  padding-right: 10px;
  padding-left: 0; }
  @media only screen and (max-width: sm-big) {
    .grid__col-1-6 {
      width: calc(8.33333% * 8);
      padding-right: 0;
      padding-left: 0; } }
  @media only screen and (max-width: xs-big) {
    .grid__col-1-6 {
      width: calc(8.33333% * 12);
      padding-right: 0;
